@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,600');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$color-primary:#0A4163;
$color-success:#4B9839;

.credential {
    &-wrapper {

        height: 100vh;
        background: $color-primary;
        display: flex;
        overflow: auto;
        padding:30px;
        * {
            font-family: 'Source Sans Pro', sans-serif;
            margin: 0;
            padding: 0;
            box-sizing: border-box;
            color:#333;
        }
        a{
            color:$color-primary;
            text-decoration: none;
            font-size: 14px;
            &:hover{
                color:darken($color-primary, 10%);
            }
        }
        .space-between{
            display: flex;
            // margin-bottom: 20px;
            min-width: 100%;
            flex-flow: row wrap;
            align-items: center;
            justify-content: space-between;
            @media screen and (max-width: 350px){
                display:block;
                text-align: center
            }
        }
    }
    &-container {
        margin: auto;
        max-width: 450px;
        width: 100%;
        text-align: center;
    }
    &-block {
        text-align: left;
        width: 100%;
        background: #fff;
        border-radius: 4px;
        margin-top: 30px;
        padding: 20px;
    }
    &-logo{
        min-height: 80px;
    }
    &-title {
        font-size: 28px;
        color: $color-primary;
        font-weight: 600;
        margin-bottom:15px;
    }

    &-btn{
        background:$color-success;
        color:#fff;
        padding:8px 20px;
        border-radius: 3px;
        cursor: pointer;
        @media(max-width:350px){
            width:100%;
            margin-top:15px;
            // text-align: center;
        }
    }
    
    
    &-form-group {
        position: relative;
        margin-bottom: 20px;
        label {
            display: inline-block;
            max-width: 100%;
            margin-bottom: 2px;
            font-size:14px;
            font-weight: 600;
        }
        .form-input {
            display: block;
            width: 100%;
            height: 34px;
            padding: 6px 0;
            font-size: 18px;
            line-height: 50px;
            color: #444;
            background-color: #fff;
            background-image: none;
            border:0;
            border-bottom: 1px solid #ccc;
            font-weight: 400;
            &::placeholder { color:#ccc; }
            &:focus{
                box-shadow: none;
                outline:0;
            }           
        }
        .pw-show, .pw-hide{
            position: absolute;
            right:0;
            bottom:0;
            display: block;
            height: 30px;
            width:30px;
            background-size: cover;
            cursor: pointer;
            opacity: 0.5;
            &:hover{
                opacity: 1;
            }
        }
        .pw-show{
            background-image:url("../images/pw-show.png");
        }
        .pw-hide{
            background-image:url("../images/pw-hide.png");
        }
        .checkbox{
            input[type='checkbox']{
                margin-right:5px;
            }
            font-weight:400;
        }
    }
    
}

